                <span class="return" @click="back"><img src="../../../assets/return_detail.png" width="100%" alt=""></span>
                <span class="title"></span>
                <span class="menu" @click="showMenu('搜索')"><img src="../../../assets/menu_detail.png" width="100%" alt=""></span>
            </div>
        </div>
        <div class="shuffling">
            <mt-swipe :show-indicators="false" :auto="0">
                <mt-swipe-item v-for="(item,index) in imgUrl" :key="item">
                    <img width="100%" height="100%" :src="item" alt="" v-if="item != null">
                    <img width="100%" height="100%" src="../../../assets/noImg.jpg" v-else alt="">
                </mt-swipe-item>
                <div class="describe">
                    <p>车源编号：
                        <span class="code">{{details.code}}</span>
                    </p>
                </div>
            </mt-swipe>
        </div>
        <div class="detail_message">
            <p class="name">{{details.title}}</p>
            <div class="detail_price">
                <p class="offer">
                    <span v-if="details.original_price">{{details.original_price}}万</span>
                </p>
                <p class="newoffer">新车价：
                    <span v-if="details.厂商指导价">{{details.厂商指导价}}万(含税)</span>
                </p>
            </div>
        </div>
        <div class="car_location">
            <p v-if="details.exhibition_area || details.exhibition_code" class="localtion">
                车辆位置：
                <span>{{details.exhibition_area}}区 {{details.exhibition_code}}</span>
            </p>
        </div>
        <div class="car_detail">
            <router-link :to="{name: 'configuration' , params:{id: details.vehicle_id}}">
                <p class="tittle">车辆详情
                    <i class="icon iconfont icon-jiankuohaoxiyou"></i>
                </p>
            </router-link>

            <ul>
                <li>
                    <p>{{details.mileage}}万公里</p>
                    <p>行驶里程</p>
                </li>
                <li>
                    <p>{{details.first_register_date | format}}</p>
                    <p>上牌时间</p>
                </li>
                <li>
                    <p>{{details.emission_standard}}</p>
                    <p>排放标准</p>
                </li>
                <li>
                    <p>
                        <template v-if="details.排量">{{details.排量}}L</template>
                    </p>
                    <p>排量</p>
                </li>
                <li>
                    <p>{{details.变速箱1}}</p>
                    <p>变速箱</p>
                </li>
                <li>
                    <p>
                        <template v-if="details.transfer_number">{{details.transfer_number}}</template>
                        <template v-else>0</template>
                    </p>
                    <p>过户次数</p>
                </li>
            </ul>
        </div>
        <div class="car_detail">
            <p class="tittle">车况检测
                <i class="icon iconfont icon-jiankuohaoxiyou"></i>
            </p>
            <div class="img_border">
                <!-- <img src="./test.png" alt=""> -->
            </div>
        </div>
        <template v-if="details.description">
            <div class="car_detail">
                <p class="tittle">卖家说车
                    <i class="icon iconfont icon-jiankuohaoxiyou"></i>
                </p>
                <div class="say_content img_border">
                    {{details.description}}
                </div>
            </div>
        </template>
        <div class="car_detail">
            <p class="tittle">购买流程
                <i class="icon iconfont icon-jiankuohaoxiyou"></i>
            </p>
            <div class="img_border">
                <img src="./buyStep.png" alt="">
            </div>
        </div>
        <router-view></router-view>
        <v-recommend :recommendData="recommendData"></v-recommend>
        <div class="joinForm">
            <template v-if="details.is_in_look == 0">
                <div class="detail_add" @click="joinLookCars(details)">
                    <img src="./jia.png" alt="">
                    <span>加看车单</span>
                </div>
            </template>
            <template v-else="details.is_in_look == 1">
                <span class="onlyJoin" @click="joinLookCars(details)">已加看车单</span>
            </template>
            <template v-if="details.mobiles && details.mobiles.length > 1">
                <div class="detail_tel" @click="showMobilesList"><img src="./tel.png" alt="">
                    <span>电话咨询</span>
                </div>
            </template>
            <template v-else-if="details.mobiles && details.mobiles.length == 1">
                <a class="detail_tel" :href="'tel:' + details.mobiles[0]"> <img src="./tel.png" alt="">
                    <span>电话咨询</span>
                </a>
            </template>
        </div>
        <v-menuPage ref="menuMark" @close="closeThis"></v-menuPage>
        <div class="showPhone">
            <template v-if="details.mobiles && details.mobiles.length > 1">
                <div v-for="item in details.mobiles">
                    <a :href="'tel:' + item">{{item}}</a>
                </div>
                <span @click="hideMobilesList">取消</span>
            </template>
        </div>
        <div class="gotop" @click="scrolltop"></div>
    </div>
</template>

<style lang="scss">
