    position: absolute;
    left: 0;
    top: 0;
    width: 7.5rem;
    height: 100%;
    background: white;
    .say_content {
        background: #f5f5f5;
        padding: 0.3rem 0.16rem !important;
        margin: 0 0.4rem;
        border-radius: 0.1rem;
        text-indent: 2em;
    }
    .seeCarHeader {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 0.88rem;
        display: flex;
        align-items: center;
        .seeCarHeader_content {
            width: 100%;
            margin: 0 0.24rem 0 0.24rem;
            display: flex;
            .return {
                width: 0.42rem;
                height: 0.42rem;
                position: absolute;
                z-index: 3;
                left: 0.24rem;
                top: 0.22rem;
            }
            .title {
                flex: 1;
                font-size: 0.34rem;
                text-align: center;
                color: #ffffff;
            }
            .menu {
                width: 0.44rem;
                height: 0.44rem;
            }
        }
    }
    .detail_message {
        .name {
            font-size: 0.32rem;
            width: 7rem;
            margin: 0 auto;
            line-height: 0.4rem;
            margin-top: 0.22rem;
        }
        .detail_price {
            display: flex;
            margin-left: 0.24rem;
            margin-top: 0.18rem;
            .offer {
                font-size: 0.3rem;
                margin-right: 0.12rem;
                span {
                    font-size: 0.36rem;
                    color: #ff4a46;
                }
            }
            .newoffer {
                font-size: 0.24rem;
                color: #636363;
                line-height: 0.54rem;
                span {
                    text-decoration: line-through;
                }
            }
        }
    }
    .car_location {
        display: flex;
        justify-content: space-between;
        margin-top: 0.3rem;
        padding-bottom: 0.23rem;
        border-bottom: 0.3rem solid #f0f0f0;
        p {
            font-size: 0.24rem;
        }
        .localtion {
            margin: 0 0.24rem;
            width: 100%;
            color: #666666;
            font-size: 0.28rem;
            border-top: 1px solid #ececec;
            padding-top: 0.3rem;
            span {
                float: right;
                margin-right: 0.38rem;
            }
        }
        .localtion_map {
            margin-right: 0.34rem;
        }
    }
    .car_detail {
        border-bottom: 0.3rem solid #f5f5f5;
        .tittle {
            font-size: 0.3rem;
            border-left: 0.06rem solid #ffb900;
            padding-left: 0.2rem;
            margin: 0.3rem 0 0.3rem 0.24rem;
            color: #000;
            .icon-jiankuohaoxiyou {
                float: right;
                margin-right: 0.4rem;
                font-weight: bold;
            }
        }
        .img_border {
            margin-bottom: 0.4rem;
            box-sizing: border-box;
            padding: 0 0.24rem;
            img {
                width: 100%;
            }
        }
        ul {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            padding-left: 0.86rem;
            li {
                width: 33%;
                margin-bottom: 0.32rem;
                font-size: 0.26rem;
                p {
                    font-size: 0.24rem;
                    color: #666;
                    text-align: left;
                }
                p:first-child {
                    font-weight: bold;
                    color: #000;
                    font-size: 0.28rem;
                    padding-bottom: 0.2rem;
                }
            }
        }
    }
    .joinForm {
        display: flex;
        position: fixed;
        bottom: 0;
        background: #fff;
        width: 100%;
        padding: 0.24rem 0;
        .detail_add,
        .detail_tel {
            display: flex;
            box-shadow: 0 0 0.1rem #ffb900;
            span {
                font-size: 0.3rem;
            }
        }
        .detail_add,
        a,
        .detail_tel {
            width: 3.4rem;
            height: 0.78rem;
            background: #ffb900;
            text-align: center;
            line-height: 0.78rem;
            margin-left: 0.24rem;
