            font-size: 0.3rem;
            color: #fff;
        }
    }
}
.showPhone {
    display: none;
    width: 4rem;
    height: 2.4rem;
    background: #fff;
    border-radius: 0.2rem;
    position: fixed;
    left: 50%;
    top: 50%;
    margin-left: -2rem;
    box-shadow: 0px 0px 1px 1px #ece7e7;
    overflow: hidden;
    a,
    span {
        display: block;
        width: 4rem;
        height: 0.8rem;
        line-height: 0.8rem;
        border-bottom: 1px solid #f3f3f3;
        color: #000;
        text-align: center;
        font-size: 0.24rem;
    }
    span {
        background: #ffd011;
        font-size: 0.3rem;
        color: #462b00;
    }
}
.gotop {
    position: fixed;
    bottom: 2rem;
    right: 0.2rem;
    border-radius: 2.5rem;
    width: 0.8rem;
    height: 0.8rem;
    background: url("./topIcon.png") no-repeat;
    background-size: 100%;
    z-index: 50;
    display: none;
}
.v-modal {
    opacity: 0.5;
}
.mint-popup {
    width: 100%;
    height: 100%;
}
</style>

<script>
import recommend from "../detailrecommend/detailrecommend.vue";
import menuPage from "../../public/nav.vue";
import { Toast, Popup, Swipe, SwipeItem } from "mint-ui";

export default {
    data() {
        return {
            details: {},
            imgUrl: [],
            lookStatus: true,
            recommendData: {},
            customerId: "",
            tokens: "",
            nativeHref: "",
            popupVisible: false
        };
    },
    created() {},
    mounted() {
        if (window.localStorage.getItem("user")) {
            if (window.localStorage.getItem("item")) {
                this.joinLookCars(
                    JSON.parse(window.localStorage.getItem("item"))
                );
                window.localStorage.setItem("item", "");
            }
        }

        var city = window.localStorage.getItem("city"),
            reg = new RegExp('"', "g");
        if (city != null) {
            city = city.replace(reg, "");
            city = Number(city);
        } else {
            window.localStorage.setItem("city", "370100"); //默认济南市
        }

        if (window.localStorage.getItem("user")) {
            this.customerId = JSON.parse(
                window.localStorage.getItem("user")
            ).customer;
            this.tokens = JSON.parse(window.localStorage.getItem("user")).token;
            if (window.localStorage.getItem("item")) {
                this.joinLookCars(
                    JSON.parse(window.localStorage.getItem("item"))
                );
                window.localStorage.setItem("item", "");
            }
        }
        let id = this.$route.params.id;
        var _this = this;
        var token = this.tokens;
        //获取详情
        $.ajax({
            type: "GET",
            url:
                this.global.host +
                "inventory/details/" +
                id +
                "?token=" +
                token,
            success: function(response) {
                if (response.resultCode == "0000") {
                    _this.details = response.result.details;
                    var original_price = _this.details.original_price,
                        min_price,
                        max_price;
                    if (original_price < 5) {
                        min_price = 0;
                        max_price = 4.99;
                    } else if (original_price > 5 && original_price < 10) {
                        min_price = 5;
                        max_price = 10;
                    } else if (original_price > 10 && original_price < 20) {
                        min_price = 10;
                        max_price = 20;
                    } else if (original_price > 20 && original_price < 30) {
                        min_price = 20;
                        max_price = 30;
                    } else if (original_price > 30 && original_price < 40) {
                        min_price = 30;
                        max_price = 40;
                    } else if (original_price > 40) {
                        min_price = 40;
                    }

                    var typelevel = _this.details.type_level;
                    var type_id = _this.details.type_id;
                    var vehicle_id = _this.details.vehicle_id;
                    detailrecommend(
                        _this,
                        type_id,
                        typelevel,
                        min_price,
                        max_price,
                        vehicle_id
                    );
                    let imgsUrl = response.result.details.images;
                    let image = [];
                    if (imgsUrl) {
                        imgsUrl.forEach(item => {
                            image.push(item);
                        });
                        _this.imgUrl = image;
                    }
                    var storage = window.localStorage;
                    storage.setItem("detailImgUrl", imgsUrl);
                } else if (response.resultCode == "1111") {
                }
            }
        });

        function detailrecommend(
            _this,
            type_id,
            typelevel,
            min_price,
            max_price,
            vehicle_id
        ) {
            //获取推荐
            var data = {
                locationCity: city,
                type_id: type_id,
                type_level: typelevel,
                min_price: min_price,
                max_price: max_price,
                token: "",
                vehicle_id: vehicle_id
            };
            $.ajax({
                type: "POST",
                url: _this.global.host + "/inventory/detailRecommend",
                data: data,
                success: function(response) {
                    if (response.resultCode == "0000") {
                        _this.recommendData = response.result.list;
                    } else if (response.resultCode == "1111") {
                    }
                }
            });
        }
    },
    methods: {
        //显示菜单
        showMenu(msg) {
            this.$refs.menuMark.show(msg, $(".cars_detail"));
        },
        closeThis() {
            this.showThis = false;
        },
        back() {
            this.$router.go(-1);
        },
        showMobilesList() {
            $(".showPhone").fadeIn();
        },
        hideMobilesList() {
            $(".showPhone").fadeOut();
        },
        joinLookCars(details) {
            var _this = this;
            if (window.localStorage.getItem("user")) {
                if (details.is_in_look == 1) {
                    $.ajax({
                        type: "POST",
                        url: _this.global.host + "watchList/remove",
                        data: {
                            token: JSON.parse(
                                window.localStorage.getItem("user")
                            ).token,
                            vehicleId: details.vehicle_id
                        },
                        success: function(response) {
                            if (response.resultCode === "0000") {
                                Toast("取消加入看车单");
                                details.is_in_look = 0;
                            } else if (response.resultCode === "1111") {
                                Toast("操作失败");
                            }
                        }
                    });
                } else {
                    $.ajax({
                        type: "post",
                        url: _this.global.host + "/watchList/add",
                        data: {
                            customerId: JSON.parse(
                                window.localStorage.getItem("user")
                            ).customer,
                            token: JSON.parse(
                                window.localStorage.getItem("user")
                            ).token,
                            vehicleId: details.vehicle_id
                        },
                        success: function(response) {
                            if (response.resultCode == "0000") {
                                details.is_in_look = 1;
                                Toast("加入看车单成功");
                            } else if (response.resultCode == "1111") {
                                Toast("加入看车单失败");
                            }
                        }
                    });
                }
            } else {
                window.localStorage.setItem("item", JSON.stringify(details));
                _this.$router.push("/login");
            }
        },
        gotop() {
            let $nav2Top = $(".gotop");
            $(window).scroll(function() {
                let scrollTop = $(this).scrollTop();
                let windowHeight = $(this).height();
                if (scrollTop >= windowHeight) {
                    $nav2Top.show();
                } else {
                    $nav2Top.hide();
                }
            });
        },
        scrolltop() {
            $(window).scrollTop(0);
        },
        closeOnClickModal() {
            this.popupVisible = false;
        }
    },
    components: {
        "v-recommend": recommend,
        "v-menuPage": menuPage
    }
};
</script>



