<template>
    <div class="listtemplate" style="padding-bottom: 1.6rem;">
        <template v-if="recommendData.length > 0">
            <p class="recommend_title">相似推荐</p>
        </template>
        <ul>
            <li :id="'carList_' + index" v-for="(item,index) in recommendData" @click="saveHistory(item)">
                <router-link :to="{name: 'detail' , params:{id: item.vehicle_id}}" @click.native="refresh()">
                    <img class="car" v-if="item.thumbnail != null" v-lazy="item.thumbnail"/>
                    <img class="car" v-else src="../../../assets/noImg.jpg"/>
                </router-link>

                <div class="car_mess">
                    <div class="pub_height">
                        <p class="car_name" @click="detailToDetail(item.vehicle_id)">{{item.title}}</p>
                        <p class="car_time">
                            <template v-if="item.first_register_date">
                                <span class="listTime">{{item.first_register_date | listTime}}</span>
                            </template>
                            <template>
                                /
                            </template>
                            <!--<template v-if="item.first_register_date && item.mileage">-->
                            <!--/-->
                            <!--</template>-->
                            <template>
                                <span class="mileage">{{item.mileage}}万公里</span>
                            </template>
                            <template v-if="item.mileage && item.exhibition_area">
                                /
                            </template>
                            <template v-if="item.exhibition_area">
                                /{{item.exhibition_area}}区{{item.exhibition_code}}
                            </template>
                        </p>
                    </div>

                    <div class="price_border">
                        <span class="show_price">{{item.original_price}}万</span>
                        <!-- <span v-for="tag in item.tags">
                          <span class="tag" :class="{hand : tag == '练手', suv : tag == 'SUV', beNew : tag == '准新'}">{{tag}}</span>
                        </span> -->
                        <span v-for="tag in item.tags" class="tag"
                              :class="{hand : tag == '练手', suv : tag == 'SUV', beNew : tag == '准新'}">{{tag}}</span>

                        <template v-if="item.is_in_look == 0">
                            <span :id="'to_look' + index" class="to_look" @click="addCarList(item)"></span>
                        </template>
                        <template v-else="item.is_in_look == 1">
                            <span class="looked" @click="removeCarList(item)"></span>
                        </template>
                    </div>
                </div>
            </li>
        </ul>
    </div>
</template>

<style lang="scss" scoped>
    .listtemplate {
        .recommend_title {
            font-size: .34rem;
            color: #191919;
            border-left: .08rem solid #ffb900;
            margin: .18rem 0 .3rem .24rem;
            padding-left: .14rem;
            height: .45rem;
            line-height: .48rem;
        }
        width: 100%;
        height: auto;
        .loading {
            margin-top: 40%;
        }
        ul {
            margin-top: .3rem;
            li {
                display: flex;
                border-bottom: 1px solid #f2f2f2;
                padding-bottom: .2rem;
                &:not(:first-child) {
                    margin-top: .3rem;
                }
                .car {
                    width: 2.20rem;
                    height: 1.64rem;
                    margin-left: .24rem;
                }
                .car_mess {
                    margin-left: .22rem;
                    height: 1.64rem;
                    position: relative;
                    a {
                        text-decoration: none;
                        display: block;
                    }
                    .pub_height {
                        height: 1.26rem;
                    }
                    .car_name {
                        color: #222;
                        width: 4.54rem;
                        font-size: .26rem;
                        font-weight: bold;
                        line-height: .4rem;
                        display: -webkit-box;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                        overflow: hidden;
                    }
                    .car_time {
                        margin: .08rem 0 .16rem 0;
                        height: .3rem;
                        span {
                            font-size: .22rem;
                            color: #666;
                        }
                    }
                    .price_border {
                        display: flex;
                        line-height: 1;
                        margin-top: .1rem;
                        .tag {
                            display: inline-block;
                            font-size: .22rem;
                            border-radius: .04rem;
                            width: .7rem;
                            height: .28rem;
                            line-height: .28rem;
                            text-align: center;
                        }
                        span:not(:first-child) {
                            margin-left: .1rem;
                        }
                        .hand {
                            border: 1px solid #666;
                            color: #666;
                        }
                        .beNew {
                            border: 1px solid #ffba00;
                            color: #ffba00;
                        }
                        .suv {
                            border: 1px solid #5d97ff;
                            color: #5d97ff;
                        }
                        .show_price {
                            float: left;
                            font-size: .3rem;
                            color: #ff0000;
                        }
                        .to_look {
                            position: absolute;
                            right: 0;
                            bottom: 0;
                            width: .44rem;
                            height: .5rem;
                            background: url('../../../assets/love.png') no-repeat;
                            background-size: .38rem;
                        }
                        .looked {
                            position: absolute;
                            right: 0;
                            bottom: 0;
                            width: .44rem;
                            height: .5rem;
                            background: url('../../../assets/loved.png') no-repeat;
                            background-size: .38rem;
                        }
                    }
                }
            }
        }
    }
</style>

<script>
    import Vue from "vue";
    import {Toast} from 'mint-ui';

    export default {
        props: {
            recommendData: {}
        },
        data() {
            return {
                s: [],
                customerId: '',
                tokens: '',
            }
        },
        mounted() {
            if (window.localStorage.getItem("user")) {
                this.customerId = JSON.parse(window.localStorage.getItem("user")).customer;
                this.tokens = JSON.parse(window.localStorage.getItem("user")).token;
            }
        },
        methods: {
            addCarList(item) {
                var _this = this;
                if (item.is_in_look == 1) {
                    return;
                } else {
                    if (window.localStorage.getItem("user")) {
                        $.ajax({
                            type: "post", url: _this.global.host + "/watchList/add",
                            data: {
                                customerId: JSON.parse(window.localStorage.getItem("user")).customer,
                                token: JSON.parse(window.localStorage.getItem("user")).token,
                                vehicleId: item.vehicle_id
                            },
                            success: function (response) {
                                if (response.resultCode == '0000') {
                                    item.is_in_look = 1;
                                    Toast("加入看车单成功");
                                } else if (response.resultCode == '1111') {
                                    Toast("加入看车单失败");
                                }
                            }
                        });
                    } else {
                        if (_this.native.isiOS) {
                            if (_this.nativeHref.indexOf("native") > 0) {
                                window.webkit.messageHandlers.login.postMessage("");
                                return;
                            } else {
                                window.localStorage.setItem("item", JSON.stringify(item))
                                _this.$router.push('/login');
                            }
                        }
                        if (_this.native.isAndroid) {
                            if (_this.nativeHref.indexOf("native") > 0) {
                                window.androidMethod.login();
                                return;
                            } else {
                                window.localStorage.setItem("item", JSON.stringify(item))
                                _this.$router.push('/login');
                            }
                        }
                        window.localStorage.setItem("item", JSON.stringify(item))
                        _this.$router.push('/login');
                    }
                }
            },
            removeCarList(item) {
                var _this = this;
                $.ajax({
                    type: "POST", url: this.global.host + 'watchList/remove',
                    data: {
                        customerId: _this.customerId,
                        token: _this.tokens,
                        vehicleId: item.vehicle_id,
                    },
                    success: function (response) {
                        if (response.resultCode === '0000') {
                            Toast("取消加入看车单");
                            item.is_in_look = 0;
                        } else if (response.resultCode === '1111') {
                            Toast("操作失败");
                        }
                    }
                });
            },
            saveHistory(item) {
                var Storage = window.localStorage;
                var ifHas = Storage.getItem("History");
                if (ifHas) {
                    var newStorage = JSON.parse(ifHas);
                    if (newStorage.length == 50) {
                        newStorage.pop();
                        newStorage.unshift(item);
                        Storage.setItem("History", JSON.stringify(newStorage));
                    } else {
                        newStorage.unshift(item)
                        Storage.setItem("History", JSON.stringify(newStorage));
                    }
                } else {
                    this.s.unshift(item)
                    Storage.setItem("History", JSON.stringify(this.s));
                }
            },
            detailToDetail(msg) {
                var _this = this;
                _this.$router.push('./' + msg);
            },
            refresh(){
                this.$router.go(0);
            }
        },
    }
    Vue.filter('listTime', function (value) {//value为13位的时间戳
        function addt(m) {
            return m < 10 ? '0' + m : m
        }

        var time = new Date(parseInt(value));
        var y = time.getFullYear();
        var m = time.getMonth() + 1;
        var d = time.getDate();

        return y + '年' + addt(m) + '月';
    });
</script>



