<template>
    <div class="menuMark" v-show="showNav">
      <div class="menu_item">
        <span class="closeMenu"><img @click="closeMenu" src="../../assets/menu-cancle.png" alt=""></span>
        <div class="menuContent">
          <router-link to="/" class="menuMark_item" v-show="!(this.msg == '首页')">
            <dl @click="closeNav">
              <dt><img src="../../assets/menu-main.png" alt=""></dt>
            </dl>
          </router-link>

          <router-link to="/search" class="menuMark_item"  v-show="!(this.msg == '搜索')">
            <dl @click="closeNav">
              <dt><img src="../../assets/menu-search.png" alt=""></dt>
            </dl>
          </router-link>

          <a @click="closeSeeList" class="menuMark_item"  v-show="!(this.msg == '看车单')">
            <dl>
              <dt><img src="../../assets/menu-seeCar.png" alt=""></dt>
            </dl>
          </a>

          <router-link to="/list" class="menuMark_item" v-show="!(this.msg == '买车')">
            <dl @click="closeNav">
              <dt><img src="../../assets/menu-buyCar.png" alt=""></dt>
            </dl>
          </router-link>

          <router-link to="/" class="menuMark_item" v-show="!(this.msg == '市场地图')">
            <dl @click="closeNav">
              <dt><img src="../../assets/market-map.png" alt=""></dt>
            </dl>
          </router-link>

        </div>
      </div>
    </div>
</template>
<style rel="stylesheet/scss" lang="scss">
  .menuMark {
    position: fixed;
    z-index: 100;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);
    top: 0;
    left: 0;
    .menu_item {
      width: 100%;
      display: flex;
      flex-direction: column;
      margin: 0.15rem auto;
      .closeMenu {
        width: 7.5rem;
        margin:0 auto;
        text-align: right;
        box-sizing: border-box;
        padding-right: 0.33rem;
        padding-top: 0.04rem;
        img{
          width:0.47rem;
          height:0.76rem;
        }
      }
      .menuContent {
        margin-top: 0.08rem;
        width: 7.5rem;
        box-sizing: border-box;
        padding: .44rem 0;
        margin: 0 auto;
        -webkit-border-radius: 0.2rem;
        -moz-border-radius: 0.2rem;
        border-radius: 0.2rem;
        background: #fff;
        display: flex;
        justify-content: space-around;
        .menuMark_item {
          text-decoration: none;
          position: relative;
          display: flex;
          justify-content: center;
          align-items: center;
          overflow: hidden;
          &.bottom {
            border-bottom: 1px solid #eeeeee;
          }
          dl {
            text-align: center;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            dt {
              display: flex;
              align-items: center;
              justify-content: center;
              width: 1.2rem;
              height: 1.2rem;
              text-align: center;
              img {
                display: block;
                height: 100%;
              }
            }
            dd {
              text-align: center;
              margin-top: 0.14rem;
              font-size: 0.24rem;
              color: #000;
            }
          }
        }
      }
    }
  }
</style>
<script>
  export default {
    data() {
      return {
        showNav: false,
        showSeeCarList:'',
        msg: '',
        tag_item: ''
      }
    },
    methods: {
      show(msg,tag){
        this.showNav = true
        this.msg = msg;
        tag.css({"overflow":"hidden"});
        this.tag_item = tag;
      },
      closeMenu() {
        this.showNav = false
        this.tag_item.css({"overflow":""});
      },
      closeNav(){
        this.showNav = false;
        this.tag_item.css({"overflow":""});
        this.$emit('close','false')
      },
      closeSeeList(){
        if(window.localStorage.getItem("user")){
          this.$router.push('/seeCarList');
          this.showNav = false;
          this.$emit('close','false');
        }else{
          this.$router.push('/login');
          this.showNav = false;
          this.$emit('close','false');
        }
      }
    }
  }
</script>



